.bubbles{stroke-width:1px;stroke:darkslategray;opacity:0.6}.bubbles:hover{stroke:red;stroke-width:2px;opacity:1;transition:color, 1s;transition:opacity, 1s}text{color:black}.button-container{display:flex;align-items:center;flex-direction:column;justify-content:center;width:20%}body{display:flex;flex-direction:row-reverse;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.danceability,.instrumentalness,.valence,.speechness,.acousticness,.liveness{padding:5px;margin-left:5px;font-family:Verdana}button{border:none;width:120px;height:40px;background:gray;color:white;margin:5px;padding:3px;border-radius:10px}button:focus{outline:none}button:hover{cursor:pointer;transition:background-color, 1.5s;background-color:#0a0909}.description{font-family:Verdana;width:370px;font-size:12px}.desc-label{font-family:Verdana;font-weight:600}.fab:hover{cursor:pointer}svg.svg-inline--fa.fa-github.fa-w-16{margin-top:5px;color:gray;width:30px;height:30px}svg.svg-inline--fa.fa-linkedin.fa-w-14{margin-top:5px;color:gray;width:30px;height:30px}
